import React, { Component } from "react";
import ReactDOM from 'react-dom';
import { AppContainer } from 'react-hot-loader';
import App from "./App.js";
import "./index.css";

import * as THREE from 'three';

var scene = new THREE.Scene();
var camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
camera.position.z = 100;

var renderer = new THREE.WebGLRenderer();
renderer.setSize(window.innerWidth, window.innerHeight);

var geometry = new THREE.BoxGeometry(100, 100, 1);
var material = new THREE.MeshBasicMaterial({ color: 0x00ffFF });
var cube = new THREE.Mesh(geometry, material);
scene.add(cube);

function animate() {
    requestAnimationFrame(animate);
    renderer.render(scene, camera);
}
animate();

const render = () => {
    //ReactDOM.render(<AppContainer><App /></AppContainer>, window.document.getElementById('app'));
}
if (typeof window !== 'undefined') { render(); }

if (module.hot) {
    module.hot.accept(render);
}



document.getElementById("3d-env").appendChild(renderer.domElement);